import { createGlobalStyle } from 'styled-components';


export const GlobalStyles = createGlobalStyle`
  html {
    --color-blue: #312E81;
    --color-blue-90: #3D3B73;
    --color-yellow: #FDE047;
    --color-yellow-90: #E6CA40; //default yellow saturation 90%
    --color-gray: #E2E8F0;
    --color-white: #FFFFFF;
    --color-danger: #EF4444;
    --color-red-dark: #9F1239;
  }
`;